#root{
	height: 100%;
}
.login_page{
    width: 100%;
    height: 100%;
    background:url(../../assests/bg_login.jpg) no-repeat center;
    display: flex;
    align-items: center;
}
.login-wrap{
	width:95%;
	margin:auto;
	max-width:450px;
	min-height:675px;
	height: auto;
	position:relative;
	box-shadow:0 12px 15px 0 rgba(0,0,0,.24),0 17px 50px 0 rgba(0,0,0,.19);
}
.login-content{
	width:100%;
	height:100%;
	position:absolute;
	padding:50px 70px;
	background:rgba(68, 76, 94, 0.9);
}
.login-content h2,
.login-form .group .button{
	text-transform:uppercase;
}
.login-content h2{
	font-size:22px;
	padding-bottom:5px;
	width: 100%;
	text-align: center;
}
.login-form{
	min-height:345px;
	position:relative;
	perspective:1000px;
}
.login-form .group{
	margin-bottom:15px;
}
.login-form .group .label,
.login-form .group .input,
.login-form .group .button{
	width:100%;
	color:#fff;
	display:block;
    outline: 0;
}
.login-form .group .input,
.login-form .group .button{
	border:none;
	padding:15px 20px;
	border-radius:25px;
	background:rgba(255,255,255,.1);
}
.login-form .group input[data-type="password"]{
	-webkit-text-security:circle;
}
.login-form .group .check + label{
    padding: 0px 10px;
    color: rgba(0,0,0,.7);
}
.login-form .group .label{
	color:#aaa;
	font-size:12px;
}
.login-form .group .button{
	background:#1161ee;
	margin: 30px 0px;
}
.login-form .group .button:hover{
    opacity: 0.8;
}
.hr{
	height:2px;
	margin: 20px 0;
	background:rgba(255,255,255,.2);
}
.link-redirect {
	text-align:center;
}
.sign-in .link-redirect a:hover,
.sign-up .link-redirect label a:hover{
    opacity: 0.8;
}